<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="cancel" 
      :width="800"
    >
      <div slot="title">出库商品内容</div>

      <div class="modal-body">

        <a-row :gutter="16">
          <a-col :span="24" style="width: 600px; margin-bottom: 12px;">
            <span class="label">麻袋sku:</span>
            <span class="value"> {{ package_sku }} </span>
          </a-col>
        </a-row>

        <a-row :gutter="16">
          <a-col :span="24" style="width: 600px; margin-bottom: 12px;">
            <a-input ref="purchase_product_id" v-model="purchase_product_id" placeholder="采购商品输入.." allowClear @pressEnter="confirm" />
          </a-col>

          <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
            <a-button type="primary" icon="search" @click="confirm">确定</a-button>
          </a-col>
        </a-row>

        <!-- 产品列表 -->
        <a-row v-for="(product, index_product) in form.purchase_products" :key="product.id" style="margin-bottom: 12px; display: flex; align-items: center; border: 1px dotted black; padding: 10px">
          <div> {{ index_product+1 }}. </div>
          <div> id: </div> 
          <div style="color: red;"> {{ product.id }} </div>
          <div style=" margin-left: 20px;"> sku: </div> 
          <div style="color: skyblue;">{{ product.sku }}</div>
          <div style=" margin-left: 20px;"> 类型: </div> 
          <div style="color: skyblue;">{{ product.type }}</div>
          <div style=" margin-left: 20px;"> 状态: </div> 
          <div style="color: skyblue;">{{ product.status }}</div>
        </a-row>
      </div>
    </a-modal>
  </div>
</template>

<script>
import { getDataWithOzonOrderId,stockOutPurchaseProductInOzonOrder } from '@/api/packer'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form', 'package_sku'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      purchase_product_id:"",
      iframe: false
    };
  },
  computed: {
  },
  watch: {
    form:{
      handler(newValue){
        this.$nextTick(() => {
          this.$refs.purchase_product_id.focus();
        });
      },
      deep:true
    }

  },
  methods: {
    confirm() {
      this.loading = true;
      
      stockOutPurchaseProductInOzonOrder({ozon_order_id:this.form.id,purchase_product_id:this.purchase_product_id}).then(
        res => {
          if(res.data){
            // 触发打印
            const pdfBase64 = res.data;
            const pdfBlob = base64ToBlob(pdfBase64);
            const pdfUrl = URL.createObjectURL(pdfBlob);

            // 创建并加载 iframe
            if(this.iframe == false){
              this.iframe = document.createElement('iframe');
              document.body.appendChild(this.iframe);
            }

            let iframe = this.iframe
            iframe.style.display = 'none'; // 隐藏 iframe
            iframe.src = pdfUrl;

            iframe.onload = () => {
              iframe.contentWindow.focus();
              iframe.contentWindow.print(); // 触发打印
              this.$emit('update');
            };

            // 将 base64 编码的 PDF 转换为 Blob
            function base64ToBlob(base64) {
              const byteCharacters = atob(base64);
              const byteArrays = [];

              for (let offset = 0; offset < byteCharacters.length; offset += 1024) {
                const slice = byteCharacters.slice(offset, offset + 1024);
                const byteNumbers = new Array(slice.length);
                for (let i = 0; i < slice.length; i++) {
                  byteNumbers[i] = slice.charCodeAt(i);
                }
                byteArrays.push(new Uint8Array(byteNumbers));
              }

              return new Blob(byteArrays, { type: 'application/pdf' });
            }
            
          }else{
            // 更新一下目前的form
            getDataWithOzonOrderId({ozon_order_id:this.form.id}).then(
              res => {
                this.form = res.data
                message.success(res.msg)
              }
            )            
          }
          
        }
      ).finally(() => {
        this.loading = false;
      })
    },
    cancel() {
      this.$emit('update');
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.label{
  width: 100px;
  margin-right: 5px;;
}

.value{
  margin-right: 5px;
  color: green;
}
</style>